.accordion__toggle {
  position: absolute;
  top: 50%;
  right: 21px;
  width: 18px;
  height: 18px;
  padding: 0;
  background-color: transparent;
  border: 0;
  transform: translateY(-50%);
  opacity: 0.4;
  transition: opacity var(--transition-duration) var(--transition-timing-function);
  appearance: none;
